
import {computed, defineComponent, inject, ref} from "vue";
import {Todo} from "@/types/todo";

export default defineComponent({
  name: "Item",
  props: {
    todo:{
      type: Object as ()=> Todo,
      required:true
    },
    index:{
      type: Number,
      required:true
    }
  },
  setup(props){
    const bgColor = ref('white');
    const myColor = ref('black');
    const isShow = ref(false);
    const mouseHandler = (flag:boolean)=> {
      if (flag){
        bgColor.value = 'pink';
        myColor.value = 'green';
        isShow.value = true;
      }
      else{
        bgColor.value = 'white';
        myColor.value = 'black';
        isShow.value = false;
      }
    }

    const updateTodoStatus = inject('updateTodoStatus', Function, true);
    const deleteTodo = inject('deleteTodo', Function, true);

    const del = ()=>{
      if (window.confirm("是否删除该任务")) {
        deleteTodo(props.index);
      }
    }
    const upStatus = computed({
      get(){
        return props.todo.isSelected;
      },
      set(val:boolean){
        updateTodoStatus(props.todo, val);
      }
    })

    return {
      mouseHandler,
      bgColor,
      myColor,
      isShow,
      del,
      upStatus,
    }
  }
})
