
import {defineComponent, ref} from "vue";

export default defineComponent({
  name: "Header",
  props:{
    addTodo:{
      type:Function,
      required:true
    }
  },

  setup(props){

    const title = ref('');
    const add = ()=>{
      if(!title.value.trim()) return;
      props.addTodo?.({
        id: Date.now(),
        title: title.value,
        isSelected: false
      });
      title.value='';
    }

    return {
      title,
      add,
    }
  }
})
